import React from 'react';

import Papa from 'papaparse';

import Chart from './components/line-chart';
import Form from './components/form';

import './App.css';

const DEATHS = "https://raw.githubusercontent.com/CSSEGISandData/COVID-19/master/csse_covid_19_data/csse_covid_19_time_series/time_series_covid19_deaths_global.csv";

const CONFIRMED = "https://raw.githubusercontent.com/CSSEGISandData/COVID-19/master/csse_covid_19_data/csse_covid_19_time_series/time_series_covid19_confirmed_global.csv";

const NO_LOCATION_NAME = '???';

function makeName(row) {
  const state = row[0];
  const country = row[1];

  if (country && state) {
    return `${country} - ${state}`
  } else if (country) {
    return country;
  } else if (state) {
    return state;
  }
  return NO_LOCATION_NAME;
}

export default class App extends React.Component {
  constructor() {
    super();
    this.state = {
      confirmed: {
        data: [],
      },
      deaths: {
        data: [],
      },
      keys: [],
      locations: [
        'Italy',
        'Spain',
        'France',
        'US',
      ],
      period: 30,
      scale: 'log',
    }
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount() {
    this.getData(this.state);
  }

  getData(configuration) {
    const {confirmed, deaths} = this.state;

    if (!deaths.data.length) {
      window.fetch(DEATHS)
        .then(response => response.text())
        .then(body => this.parseRawData(body))
        .then(parsed => this.updateState(parsed, configuration, 'deaths'))
    } else {
      this.updateState([deaths.header, deaths.rows], configuration, 'deaths');
    }

    if (!confirmed.data.length) {
      window.fetch(CONFIRMED)
        .then(response => response.text())
        .then(body => this.parseRawData(body))
        .then(parsed => this.updateState(parsed, configuration, 'confirmed'))
    } else {
      this.updateState([confirmed.header, confirmed.rows], configuration, 'confirmed');
    }
  }

  parseRawData(rawData) {
    const _rows = Papa.parse(rawData).data
    const header = _rows[0];
    const rows = _rows.slice(1)
      .sort((a,b) => makeName(a).localeCompare(makeName(b)))

    // remove rows without country or state
    while (true) {
      if (makeName(rows[0]) !== NO_LOCATION_NAME) {
        break;
      }
      rows.shift()
    }

    return [header, rows];
  }

  updateState([header, rows], configuration, dataKey) {
    const {
      locations,
      period,
      scale,
    } = configuration;

    const tsOffset = 4;
    let offset = tsOffset;
    if (period !== 0) {
      offset = header.length - period;
    }
    const timestamps = header.slice(offset);

    let keys = new Set();
    const locationSet = new Set(locations);
    const data = timestamps.map((timestamp, idx) => {
      const datum = {
        timestamp,
      };
      rows.forEach(row => {
        const name = makeName(row);
        keys.add(name);
        if (locationSet.has(name)) {
          datum[name] = row[offset + idx];
        }
      });
      return datum;
    });
    keys = Array.from(keys);
    const newState = {
      [dataKey]: {
        data,
        header,
        rows,
      },
      keys,
      locations,
      period,
      scale
    }
    this.setState(newState);
  }

  onSubmit(configuration) {
    this.getData(configuration);
  }

  render() {
    const {
      confirmed,
      deaths,
      keys,
      locations,
      period,
      scale,
    } = this.state;
    return (
      <div className="App">
        <div className="content">
          <h1 className="App-Header">The Coronavirus Curve</h1>
          <div className={'App-Form'}>
            <Form
              periodSelectedValue={period}
              locationSelectedValues={locations}
              scaleSelectedValue={scale}
              keys={keys}
              onSubmit={this.onSubmit} />
          </div>

          <h2 className="App-Header">Deaths</h2>
          <div className={'App-Chart-container'}>
            <div>
              <Chart
                data={deaths.data}
                keys={locations}
                scale={scale} />
            </div>
          </div>

          <h2 className="App-Header">Confirmed Cases</h2>
          <div className={'App-Chart-container'}>
            <div>
              <Chart
                data={confirmed.data}
                keys={locations}
                scale={scale} />
            </div>
          </div>

          <div className={'spacer'}></div>

          <div className={'about'}>
            {'Data: '}
            <a href="https://github.com/CSSEGISandData/COVID-19">Johns Hopkins CSSE</a>
          </div>

          <div className={'spacer'}></div>


          <h2 className="App-Header">Understanding the Data</h2>
          <div className={'spacer'}></div>
          <div className={'center video-container'}>
            <iframe
              src="https://www.youtube.com/embed/Kas0tIxDvrg"
              frameborder="0"
              title="Understanding the data"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen>
            </iframe>
          </div>

          <div className={'spacer'}></div>

          <div className={'about'}>
            {'Credit: '}
            <a href="https://www.youtube.com/channel/UCYO_jab_esuFRV4b17AJtAw">3Blue1Brown</a>
          </div>

          <div className={'spacer'}></div>

          <div className={'footer'}></div>
        </div>
      </div>
    );
  }
}

